.filter-controls{
  display: flex;
  flex-direction: column;
  align-items: center;
 /* justify-content: space-between; */
  margin-top: 0; /* Center horizontally */
  margin-bottom: 0; /* Center horizontally */
  height: 100%; /* Ensure full height for vertical centering */
  width: 100%;
  position: relative;
}

.filter-controls__selection-controls {
  /* display: flex; */
  /* flex-direction: row; */
  /* justify-content: center;  */
  /* align-items: center;     */
  /* width: 100%;    */
  display: block;
  width: 100%;
  box-sizing: border-box;
}

.selection-controls-group {
  display: flex;
  flex-direction: row;
  /* max-width: 50% ; */
}

.selection-controls-button {
  display: block;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  object-fit: fill;
}

.filter-controls__selected-filters {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  border: 1px solid #101010;
  max-height: 20em;
  overflow-y: auto;
  background-color: lightgray;
  
    /* border-bottom: 1px solid rgb(238, 238, 238); */
    

  /* position: absolute; */
  bottom: 0
}

.filter-controls__selected-filters-header{
  /* padding-left: 0.25em; */
  text-decoration: underline;
  background-color: rgb(180,180,180);
     border-bottom: 1px solid rgb(238, 238, 238);

}

/* For WebKit browsers (Chrome, Safari) */
.filter-controls__selected-filters::-webkit-scrollbar {
  width: 8px; /* Width of the scrollbar */
}

.filter-controls__selected-filters::-webkit-scrollbar-track {
  background: #f1f1f1; /* Background of the scrollbar track */
  border-radius: 10px;
}

.filter-controls__selected-filters::-webkit-scrollbar-thumb {
  background-color: #888; /* Color of the scrollbar thumb */
  border-radius: 10px;
  border: 2px solid #f1f1f1; /* Space around thumb for padding effect */
}

.filter-controls__selected-filters::-webkit-scrollbar-thumb:hover {
  background-color: #555; /* Thumb color on hover */
}

/* For Firefox */
@-moz-document url-prefix() {
  .filter-controls__selected-filters {
    scrollbar-color: #888 #f1f1f1; /* Thumb and track color */
    scrollbar-width: thin; /* Width of the scrollbar */
  }
}

.filter-controls__filter-item {
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  justify-content: space-between;
   padding-left: 0.25em;
   border-bottom: 1px solid rgb(238, 238, 238);
}

.filter-controls__filter-item-info {
  padding: 0;
  display: flex;
}

.filter-controls__edit-button {
  padding: 0;
}

.filter-controls__filter-name {
  display: flex;
  flex-direction: row
}

.filter-controls__add-filter-button {
  padding: 0
}

.filter-controls__selected-filters-title {
  font-weight: bold;
  font-size: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center; /* Centers horizontally */
  align-items: center;     /* Centers vertically */
}

.filter-controls__apply-button {
  position: absolute;
  top: 0;
  padding: 0;
  width: 100%;
  /* margin-bottom: 2em; */
}

.filter-controls__reset-annotations {
  position: absolute;
  bottom: 0;
  width: 100%
}